.fade-appear {
  opacity: 0;
}

.fade-appear-active {
  opacity: 1;
  transition: opacity 250ms ease-in-out;
}

.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in-out;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 250ms ease-in-out;
}

.title-appear {
  transform: translateX(-100%);
}

.title-appear-active {
  transform: translateX(0);
  transition: transform 250ms linear;
}

.contacts-enter {
  transform: translateX(-100%);
}

.contacts-enter-active {
  transform: translateX(0);
  transition: transform 250ms linear;
}

.contacts-exit {
  transform: translateX(0);
}

.contacts-exit-active {
  transform: translateX(-100%);
  transition: transform 250ms linear;
}

